.uk-button-default {
  background-color: rgb(56, 71, 71);
  color: white;
  border: 1px solid #e5e5e5;
  margin: 0px 10px 0px 0px;
}

.uk-button-default:focus, .uk-button-default:hover {
  background-color: rgba(0, 135, 177, 0.582);
}

.formStyle {
  margin: 0px 0px 0px 50px;
}


html, body, #page-wrapper {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  min-height: 100vh;
  overflow: auto;
}

.welcome-page {
  background-image: url("https://img00.deviantart.net/f694/i/2015/292/0/c/nice_house_by_roman_gp-d9dqnmw.jpg");
}

.background-signup { 
  background-image: url("https://orig00.deviantart.net/6c42/f/2012/143/3/a/frankfurt_city_by_riot23-d50unfp.jpg");
}

.background-login { 
  background-image: url("https://cdn.vox-cdn.com/thumbor/CSjdZI57UTKp-BlkzUI300lIiuM=/0x0:1920x1280/2820x1586/filters:focal(807x487:1113x793):format(webp)/cdn.vox-cdn.com/uploads/chorus_image/image/52802071/2400_oakley_1.0.jpg");
}



